	onPageScroll(e) {
		return;
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
	},
	methods: {
		getOrderList(){
			this.$util.post('/gc/getOrder',{
				type:1 // 0获取所有用户的 1获取当前用户
			}).then(res=>{
				this.list = res.data.data
				this.list.forEach(item=>{
					item.payment = item.orderInfo.reduce((total,pre)=>total+=pre.price*pre.num,0)
				})
			})
		},
		onComment(item){
			uni.navigateTo({
				url: '/pages/shopList/shopList?pages=publish'+'&goodOrderNum='+item.orderNum
			})
		},
		showType(tbIndex) {
			this.tabbarIndex = tbIndex;
			// this.list = this.orderList[tbIndex];
		},
		onDetail(item){
